import { Component, Inject, OnInit } from '@angular/core';
import { FormBuilder, FormControl, FormGroup } from '@angular/forms';
import {MatDialog, MatDialogRef, MAT_DIALOG_DATA ,} from '@angular/material/dialog';
import { AgentsService, ProjectsService, AlertService } from 'src/app/services';


@Component({
  selector: 'app-assign-agent',
  templateUrl: './assign-agent.component.html',
  styleUrls: ['./assign-agent.component.scss']
})
export class AssignAgentComponent implements OnInit {

  public id
  public name
  public remo
  form: FormGroup;
  agents :[]

  constructor(public dialogRef: MatDialogRef<AssignAgentComponent>, private alert: AlertService,
    public dialog: MatDialog , @Inject(MAT_DIALOG_DATA) public data: any  ,  private mysurvey:ProjectsService, private formBuilder: FormBuilder, private myagents :AgentsService) { }

  ngOnInit() {
    this.myagents.getAllAgent().subscribe((resp:any) =>{


       this.agents = resp

       console.log(resp)
    })
    this.form = this.formBuilder.group({
      role: new FormControl(null)
    });
  this.id =this.data.id
  this.name =  this.data.name
  this.remo =  this.data.remove




  }



  onNoClick(): void {
    this.dialogRef.close();
  }

  openDialog(): void {
    const dialogRef = this.dialog.open(AssignAgentComponent, {
      height: '400px',
      width: '16900px'
       });

    dialogRef.afterClosed().subscribe(result => {
      console.log('The dialog was closed');
      return result;
    });
  }



  onSubmit(){



    if(this.remo){

      this.mysurvey.unassignAgentToSurvey(this.id,this.form.value.role).subscribe((resp =>{

        
        this.alert.success('Agent removed successfully');



      }),(err)=>{

 this.alert.error(err.error.message)


      })




    }else{

      this.mysurvey.assignAgentToSurvey(this.id,this.form.value.role).subscribe((resp=>{


        this.alert.success('Agent assigned successfully');
      }),(err) =>{
  
        this.alert.error(err.error.message);
      })




    }




    


  }


}
