import {Injectable} from '@angular/core';
import {HttpParams} from '@angular/common/http';
import {ApisService} from './apis.service'
import {Agents, Page} from '../models'

@Injectable({
  providedIn: 'root'
})
export class AgentsService {

  constructor(private apis : ApisService) {}

  // tslint:disable-next-line: typedef
  newAgent(agent: Agents) {

      return this.apis.post < Agents > (`/v1/agents`, agent)
  }



  createAgentForClient(agent: Agents) {

    return this.apis.post < Agents > (`/v1/agents/for-client`, agent)
}

  // tslint:disable-next-line: typedef
  getPaginatedAgents(filters?: any) {

      const httpParams = new HttpParams({fromObject: filters});

      return this.apis.get < Page < Agents >> (`/v1/agents/datcure-agents`, httpParams)

  }



  getAllAgent(){


    
    return this.apis.get(`/v1/agents/all`)


  }

  // tslint:disable-next-line: typedef
  getOneAgent(agentID: string | number) {

      return this
          .apis
          .get(`/v1/agents/${agentID}`);

  }

  // tslint:disable-next-line: typedef
  updateAgent(agent: Agents, agentId: string | number) {

      return this
          .apis
          .put(`/v1/agents/${agentId}`, agent);
  }

  changeUserAdminActivationStatus(activationStatus: boolean, userId: string | number) {
    const httpParams = new HttpParams().set('status', String(activationStatus));
    return this
        .apis
        .patch(`/v1/users/${userId}`, {}, httpParams);
}




 // tslint:disable-next-line: typedef
 getPaginatedClientAgents(filters?: any) {

  const httpParams = new HttpParams({fromObject: filters});

  return this.apis.get < Page < Agents >> (`/v1/agents/client-agents`, httpParams)

}




}
