import { HttpParams } from '@angular/common/http';
import {Injectable} from '@angular/core';
import {ApisService} from './apis.service'
import { Page, Question } from '../models';



@Injectable({
  providedIn: 'root'
})
export class SuveysService {


  constructor(private apis : ApisService,) {}


  postSurvey(survey:any){
    return this.apis.post(`/v1/projects` ,survey)
  }
  
  
  assignAgentToSurvey(surveyID:any,agentId:any){
  
    return this.apis.post(`/v1/projects/${surveyID}/assign-agent/${agentId}`,{})
  
  
  
  }
  
  
   // tslint:disable-next-line: typedef
   getPaginatedSurvey(filters?: any) {
  
    const httpParams = new HttpParams({fromObject: filters});
  
    return this.apis.get < Page<Question>> (`/v1/projects/all`, httpParams)
  
  }
  
  
  
  
  
  
  unassignAgentToSurvey(surveyID:any,agentId:any){
  
    return this.apis.post(`/v1/projects/${surveyID}/un-assign-agent/${agentId}`,{})
  
  
  }
  
  
  publishSurvey(surveyID){
  
    return this.apis.post(`/v1/projects/publish/${surveyID}`,{})
  
  }
  
  
  deleteSurvey(surveyID){
  
    return this.apis.delete(`/v1/projects/${surveyID}`)
  
  
  }
  
  
  DeactivateSurvey(surveyID){
  
    
    return this.apis.post(`/v1/projects/deactivate/${surveyID}`,{})
  
  
  }


   editSurvey(surveyID, survey){

    return this.apis.put(`/v1/projects/${surveyID}`,survey)


   }


   get1Survey(surveyID){


    return this.apis.get(`/v1/projects/${surveyID}`)


   }
  
  
  
  
  
  }
  