import {Injectable} from '@angular/core';
import {HttpParams} from '@angular/common/http';
import {ApisService} from './apis.service'
import {Projects, Page} from '../models'

@Injectable({
  providedIn: 'root'
})
export class ProjectsService {

  constructor(private apis : ApisService) {}

  // tslint:disable-next-line: typedef
  newUser(project: Projects) {

      return this.apis.post < Projects > (`/v1/clients`, project)
  }

  // tslint:disable-next-line: typedef
  getPaginatedProjects(filters?: any) {

      const httpParams = new HttpParams({fromObject: filters});

      return this.apis.get < Page < Projects >> (`/v1/clients`, httpParams)

  }

  // tslint:disable-next-line: typedef
  getOneProject(projectID: string | number) {

      return this
          .apis
          .get(`/v1/clients/${projectID}`);

  }



    // tslint:disable-next-line: typedef
    getAllClients() {

        return this
            .apis
            .get(`/v1/clients`);
  
    }
  // tslint:disable-next-line: typedef
  updateProject(project: Projects, projectId: string | number) {

      return this
          .apis
          .put(`/v1/clients/${projectId}`, project);
  }

  changeUserAdminActivationStatus(activationStatus: boolean, userId: string | number) {
    const httpParams = new HttpParams().set('status', String(activationStatus));
    return this
        .apis
        .patch(`/v1/users/${userId}`, {}, httpParams);
}




assignAgentToSurvey(surveyID:any,agentId:any){

  return this.apis.post(`/v1/projects/${surveyID}/assign-agent/${agentId}`,{})



}


unassignAgentToSurvey(surveyID:any,agentId:any){

  return this.apis.post(`/v1/projects/${surveyID}/un-assign-agent/${agentId}`,{})



}

}
