import {Component, EventEmitter, Input, OnInit, Output} from '@angular/core';
import {FormGroup, FormBuilder, FormArray, FormControl} from '@angular/forms';
import {ClrLoadingState} from '@clr/angular';
import {AlertService, ProjectsService, SuveysService} from 'src/app/services';

export interface InputType {
    value : string;
    viewValue : string;
}

@Component({selector: 'app-survey-form', templateUrl: './survey-form.component.html', styleUrls: ['./survey-form.component.scss']})
export class SurveyFormComponent implements OnInit {

    @Input()errorMessage : Error;
    @Input()dialog : Boolean = false
    @Input()btnState$ : ClrLoadingState;
    @Input()loading : Boolean = false;
    @Output()childSubmit : EventEmitter < any > = new EventEmitter < any > ();

    public surveyForm : FormGroup;
    clients : []
    selectedType = [];
    inputTypes : InputType[] = [
        {
            value: 'MULTIPLE_CHOICE_CHECKBOXES',
            viewValue: 'Checkbox'
        }, {
            value: 'DATE_INPUT',
            viewValue: 'Datepicker'
        }, {
            value: 'DECIMAL_INPUT',
            viewValue: 'Number'
        }, {
            value: 'SINGLE_CHOICE_RADIO_BUTTONS',
            viewValue: 'Radio Button'
        }, {
            value: 'SINGLE_CHOICE_DROP_DOWN',
            viewValue: 'Select'
        }, {
            value: 'TEXT_INPUT',
            viewValue: 'Text'
        }, {
            value: 'TEXT_AREA',
            viewValue: 'Text Area'
        }
    ];

    constructor(private fb : FormBuilder, private cli : ProjectsService, private survey : SuveysService, private alert : AlertService) {}
    ngOnInit() {

        this.createSurveyForm()

        this
            .cli
            .getAllClients()
            .subscribe((x : any) => {

                console.log(x)
                this.clients = x.content
            })

    }

    private createSurveyForm() {

        this.surveyForm = this
            .fb
            .group({
                survey_details: this
                    .fb
                    .group({client: '', name: '', description: ''}),
                questions: this
                    .fb
                    .array([this.questions])
            });

    }

    get questions() : FormGroup {
        return this
            .fb
            .group({
                type: new FormControl(),
                questionText: new FormControl(),
                required: true,
                questionOptions: this
                    .fb
                    .array([this.options])
            });
    }

    get options() : FormGroup {
        return this
            .fb
            .group({text: new FormControl()});
    }

    addQuestion() {

        (this.surveyForm.get('questions')as FormArray).push(this.questions);
    }

    deleteQuestion(index) {
        (this.surveyForm.get('questions')as FormArray).removeAt(index);
    }

    addOption(question) {
        console.log('adding questoin');

        question
            .get('questionOptions')
            .push(this.options);
    }

    deleteOption(question, index) {
        question
            .get('questionOptions')
            .removeAt(index);
    }

    publishSurvey() {

        this
            .childSubmit
            .emit(this.surveyForm.value)

        console.log(this.surveyForm.value)

        let newSurvey = {
            clientId: this.surveyForm.value.survey_details.client,
            name: this.surveyForm.value.survey_details.name,
            description: this.surveyForm.value.survey_details.description,
            questions: this.surveyForm.value.questions

        }

        this
            .survey
            .postSurvey(newSurvey)
            .subscribe((resp : any) => {

                this
                    .survey
                    .publishSurvey(resp.id)
                    .subscribe((result) => {

                        this
                            .alert
                            .success('Survey Published Successfully')

                    }, (err) => {

                        this
                            .alert
                            .error(err.error.message)
                    })

            }, (err) => {

                this
                    .alert
                    .error(err.error.message)
            })

    }

}
