import {Component, OnInit, Input, Output, EventEmitter} from '@angular/core';
import { ClrDatagridStateInterface } from '@clr/angular';
import {Users} from '../../../../models'
@Component({selector: 'app-user-ui', templateUrl: './user-ui.component.html', styleUrls: ['./user-ui.component.scss']})
export class UserUiComponent implements OnInit {
    @Input()errorMessage : Error;
    @Output()childSubmit : EventEmitter < any > = new EventEmitter < any > ();
    @Output() updatedState = new EventEmitter<ClrDatagridStateInterface>();
    @Output()edit = new EventEmitter < Users[] > ()
    @Output()enable = new EventEmitter <Users[]>()
    @Output() view = new EventEmitter <Users[]>()
    @Output()authority = new EventEmitter <Users[]>()
    @Input() total :number
    @Input() loading = false;
    dialog = false;
    @Input()users : Users;

    constructor() {}
    ngOnInit() {}

  

}
