import {
    Component,
    EventEmitter,
    Input,
    OnInit,
    Output,
    OnChanges
} from '@angular/core';
import {FormArray, FormBuilder, FormControl, FormGroup} from '@angular/forms';
import {Router, ActivatedRoute} from '@angular/router';
import { getDefaultOptions } from '@clr/core/internal';
import {SuveysService} from 'src/app/services';

export interface InputType {
    value : string;
    viewValue : string;
}

@Component({selector: 'app-edit-survey', templateUrl: './edit-survey.component.html', styleUrls: ['./edit-survey.component.scss']})
export class EditSurveyComponent implements OnInit {

    id;

    public surveyForm : FormGroup;
    currentForm = {

        name: '',
        description: '',
        client: {
            id: 0
        },
        questions: []
    };

    loader = false
    selectedType = [];
    inputTypes : InputType[] = [
        {
            value: 'MULTIPLE_CHOICE_CHECKBOXES',
            viewValue: 'Checkbox'
        }, {
            value: 'DATE_INPUT',
            viewValue: 'Datepicker'
        }, {
            value: 'DECIMAL_INPUT',
            viewValue: 'Number'
        }, {
            value: 'SINGLE_CHOICE_RADIO_BUTTONS',
            viewValue: 'Radio Button'
        }, {
            value: 'SINGLE_CHOICE_DROP_DOWN',
            viewValue: 'Select'
        }, {
            value: 'TEXT_INPUT',
            viewValue: 'Text'
        }, {
            value: 'TEXT_AREA',
            viewValue: 'Text Area'
        }
    ];

    constructor(private route : ActivatedRoute, private fb : FormBuilder, private proj : SuveysService) {}

    ngOnInit() {

        this.id = this.route.snapshot.params.id;
     
        this.OnChanges()
        this.createSurveyForm()

     
    }

    OnChanges() {

        this
            .proj
            .get1Survey(this.id)
            .subscribe((resp : any) => {

                this.currentForm = resp

                resp
                    .questions
                    .forEach(el => {

                        const ay = []




                        getDefaultOptions

                            el.optionsList
                                ?.forEach((y : any,index) => {

                                    return ({text: y.text})

                                });

                            (this.surveyForm.get('questions')as FormArray).push(this.fb.group({
                                questionOptions: [ay],
                                 questionText: el.questionText, 
                                 type: el.type, 
                                 required: true}))

                                 this.loader =true

                        });

                })

        }

        private createSurveyForm() {

            this.surveyForm = this
                .fb
                .group({
                    survey_details: this
                        .fb
                        .group({clientId: this.currentForm.client.id, name: this.currentForm.name, description: this.currentForm.description}),
                    questions: this
                        .fb
                        .array([this.questions])
                });

        }


        getOption(qId) {               

            return this
                .surveyForm.value
                .questions
                .find((e) => {
        
                    return e.id == qId
        
                })
                ?.optionsList
        
        }
        
        get questions() : FormGroup {
            return this
                .fb
                .group({
                    type: new FormControl(''),
                    questionText: new FormControl(''),
                    required: true,
                    questionOptions: this
                        .fb
                        .array([this.options])
                });
        }

        get options() : FormGroup {
            return this
                .fb
                .group({text: new FormControl()});
        }

        addQuestion() {

            (this.surveyForm.get('questions')as FormArray).push(this.questions);
        }

        deleteQuestion(index) {
            (this.surveyForm.get('questions')as FormArray).removeAt(index);
        }

        addOption(question) {
            console.log('adding questoin');

            question
                .get('questionOptions')
                .push(this.options);
        }

        deleteOption(question, index) {
            question
                .get('questionOptions')
                .removeAt(index);
        }

        publishSurvey() {

            console.log('we are updating')

        }

    }
