import {
    Component,
    OnInit,
    Input,
    Output,
    EventEmitter,
    SimpleChanges
} from '@angular/core';

import {FormGroup, FormControl, Validators, FormBuilder} from '@angular/forms';

@Component({selector: 'app-edit-project', templateUrl: './edit-project.component.html', styleUrls: ['./edit-project.component.css']})
export class EditProjectComponent implements OnInit {

  @Input()errorMessage : Error;
    @Input() project ;
    @Output() childSubmit : EventEmitter < any > = new EventEmitter < any > ();
    public templateForm : FormGroup;
    public dialog = false;
    @Input()groups : [];
    constructor(private formBuilder : FormBuilder,) {}

    ngOnInit() {

        this.createUserForm()
        this.dialog = true
        console.log(this.dialog)
    }

    private createUserForm() {
        this.templateForm = new FormGroup({
          description: new FormControl(this.project.description),
          email: new FormControl(this.project.user.email),
          name: new FormControl(this.project.name),
          phoneNumber: new FormControl(this.project.user.phoneNumber),
          username: new FormControl(this.project.user.username)

        });

    }
    close() {
        this.dialog = false
    }
    onSubmit() {

        this
            .childSubmit
            .emit(this.templateForm.value)
    }

}
