import {Injectable} from '@angular/core';
import {HttpParams} from '@angular/common/http';
import {ApisService} from './apis.service'
import {Documents, Page} from '../models'

@Injectable({
  providedIn: 'root'
})
export class DocumentsService {

  constructor(private apis : ApisService) {}

  // tslint:disable-next-line: typedef
  newDocument(document: Documents) {

      return this.apis.post < Documents > (`/v1/client-documents`, document)
  }

  uploadFile(documentUpload:any){
    return this.apis.file (`/v1/files`,documentUpload)
  }

  // tslint:disable-next-line: typedef
  getPaginatedDocuments(filters?: any) {

      const httpParams = new HttpParams({fromObject: filters});

      return this.apis.get < Page < Documents >> (`/v1/client-documents`, httpParams)

  }

  // tslint:disable-next-line: typedef
  getOneDocument(documentID: string | number) {

      return this
          .apis
          .get(`/v1/client-documents/${documentID}`);

  }

  deleteDocument(clientDocumentId: string | number){
    return this.apis.delete(`/v1/client-documents/${clientDocumentId}`);
  }


}
