import {
  Component,
  OnInit,
  Input,
  Output,
  EventEmitter,
  SimpleChanges
} from '@angular/core';

import {FormGroup, FormControl, Validators, FormBuilder} from '@angular/forms';


@Component({
  selector: 'app-edit-agent',
  templateUrl: './edit-agent.component.html',
  styleUrls: ['./edit-agent.component.css']
})
export class EditAgentComponent implements OnInit {

  @Input()errorMessage : Error;
    @Input() agent ;
    @Output()childSubmit : EventEmitter < any > = new EventEmitter < any > ();
    public templateForm : FormGroup;
    public dialog = false;
    @Input() groups:[];
    constructor(private formBuilder : FormBuilder,) {}

    ngOnInit() {

        this.createUserForm()
        this.dialog = true
        console.log(this.agent)
    }

    private createUserForm() {


        this.templateForm = new FormGroup({
            name: this
                .formBuilder
                .group({
                    firstName: new FormControl(this.agent.user.firstName),
                    lastName: new FormControl(this.agent.user.lastName),
                    username: new FormControl(this.agent.user.username),
                    gender: new FormControl(this.agent.user.gender),
                    dateOfBirth: new FormControl(this.agent.user.dateOfBirth),
                    initials: new FormControl(this.agent.user.initials),
                    title: new FormControl(this.agent.user.title),
                    id: new FormControl(this.agent.id)
                }),

            contact: this
                .formBuilder
                .group({
                    email: new FormControl(this.agent.user.email),
                    phoneNumber: new FormControl(this.agent.user.phoneNumber)
                }),

            other: this
                .formBuilder
                .group({
                    driverLicenseNumber: new FormControl(this.agent.user.driverLicenseNumber),
                    nationality: new FormControl(this.agent.user.nationality),
                    nationalIdentificationNumber: new FormControl(this.agent.user.nationalIdentificationNumber),
                    passportNumber: new FormControl(this.agent.user.passportNumber),
                   
                })

        });

    }

    onSubmit() {

        this
            .childSubmit
            .emit(this.templateForm.value)
    }

}
