import {
  Component,
  OnInit,
  Input,
  Output,
  EventEmitter,
  SimpleChanges,
} from '@angular/core';
import { FormGroup, FormControl, Validators ,FormBuilder} from '@angular/forms';


@Component({
  selector: 'app-client-agent-form',
  templateUrl: './client-agent-form.component.html',
  styleUrls: ['./client-agent-form.component.css']
})
export class ClientAgentFormComponent implements OnInit {

  @Input()errorMessage : Error;
  @Input() groups:[];
  @Input() clients:[];
  @Input() dialog :Boolean = false
  @Input() submitting:Boolean = false
   @Output()childSubmit : EventEmitter < any > = new EventEmitter < any > ();
  public templateForm : FormGroup;



  constructor(  private formBuilder : FormBuilder) {}
  ngOnInit() {

      this.createUserForm()

  }

  private createUserForm() {

      this.templateForm = new FormGroup({
          name: this
              .formBuilder
              .group({
                  clientId: new  FormControl(''),
                  firstName: new FormControl('', Validators.required),
                  lastName: new FormControl('', Validators.required),
                  username: new FormControl('', Validators.required),
                  gender: new FormControl('', Validators.required),
                  dateOfBirth: new FormControl('', Validators.required),
                  initials: new FormControl('', Validators.required),
                  title: new FormControl('', Validators.required)
              }),

          contact: this
              .formBuilder
              .group({
                  email: new FormControl('', Validators.required),
                  phoneNumber: new FormControl('', Validators.required)
              }),

          other: this
              .formBuilder
              .group({
                  driverLicenseNumber: new FormControl('', Validators.required),
                  nationality: new FormControl('', Validators.required),
                  nationalIdentificationNumber: new FormControl('', Validators.required),
                  passportNumber: new FormControl('', Validators.required),
               
              })

      });

  }
  get f(){
    return this.templateForm.controls;
  }
  onSubmit() {

      this
          .childSubmit
          .emit(this.templateForm.value)
  }

}

