import {Component, OnInit, Input, Output, EventEmitter} from '@angular/core';
import { ClrDatagridStateInterface } from '@clr/angular';
import {Projects,Documents} from '../../../../models'

@Component({
  selector: 'app-document-ui',
  templateUrl: './document-ui.component.html',
  styleUrls: ['./document-ui.component.css']
})
export class DocumentUiComponent implements OnInit {

  @Input()errorMessage : Error;
  @Output()childSubmit : EventEmitter < any > = new EventEmitter < any > ();
  @Output() updatedState = new EventEmitter<ClrDatagridStateInterface>();
  @Output() delete = new EventEmitter <Documents[]>()
  @Input() total :number
  @Input() loading = false;
  dialog = false;
  @Input() projects : Projects;
  @Input() documents : Documents;
  constructor() {}
  ngOnInit() {}



}
