import {Component, OnInit, Input, Output, EventEmitter} from '@angular/core';
import { ClrDatagridStateInterface } from '@clr/angular';
import {Agents} from '../../../../models'

@Component({
  selector: 'app-agent-ui',
  templateUrl: './agent-ui.component.html',
  styleUrls: ['./agent-ui.component.css']
})
export class AgentUiComponent implements OnInit {

  @Input()errorMessage : Error;
    @Output()childSubmit : EventEmitter < any > = new EventEmitter < any > ();
    @Output() updatedState = new EventEmitter<ClrDatagridStateInterface>();
    @Output()edit = new EventEmitter < Agents[] > ()
    @Output()enable = new EventEmitter < Agents[]>()
    @Output() view = new EventEmitter < Agents[]>()
    @Output()authority = new EventEmitter < Agents[]>()
    @Input() total :number
    @Input() loading = false;
    dialog = false;
    @Input() agents : Agents;

    constructor() {}
    ngOnInit() {}

  

}
