import {Component, OnInit, Input, Output, EventEmitter} from '@angular/core';
import { ClrDatagridStateInterface } from '@clr/angular';
import {Answers} from '../../../../models'

@Component({
  selector: 'app-answer-ui',
  templateUrl: './answer-ui.component.html',
  styleUrls: ['./answer-ui.component.css']
})
export class AnswerUiComponent implements OnInit {

  @Input()errorMessage : Error;

  @Output() updatedState = new EventEmitter<ClrDatagridStateInterface>();

  @Output() view = new EventEmitter < Answers[]>()
  @Output() map = new EventEmitter<String>()
  @Output() exportallexcel = new EventEmitter<String>() ;
  @Input() total :number
  @Input() loading = false;
  dialog = false;
  @Input() agents : Answers;

  constructor() {}
  ngOnInit() {}



}
