import {Injectable} from '@angular/core';
import {HttpParams} from '@angular/common/http';
import {ApisService} from './apis.service'
import {Users, Page} from '../models'

@Injectable({providedIn: 'root'})
export class UsersService {

    constructor(private apis : ApisService) {}

    // tslint:disable-next-line: typedef
    newUser(user: Users) {

        return this.apis.post < Users > (`/v1/users`, user)
    }

    // tslint:disable-next-line: typedef
    getPaginatedUsers(filters?: any) {

        const httpParams = new HttpParams({fromObject: filters});

        return this.apis.get < Page < Users >> (`/v1/users/admins`, httpParams)

    }

    // tslint:disable-next-line: typedef
    getOneUser(userID: string | number) {

        return this
            .apis
            .get(`/v1/users/${userID}`);

    }

    // tslint:disable-next-line: typedef
    updateUserAdmin(user: Users, userId: string | number) {

        return this
            .apis
            .put(`/v1/users/${userId}`, user);
    }

    // tslint:disable-next-line: typedef
    changeUserAdminActivationStatus(activationStatus: boolean, userId: string | number) {
        const httpParams = new HttpParams().set('status', String(activationStatus));
        return this
            .apis
            .patch(`/v1/users/${userId}`, {}, httpParams);
    }

    updateMyAccount(account: Object) {

        return this
            .apis
            .post(`/v1/users/my-account`, account)
    }

    // tslint:disable-next-line: typedef
    getMyProfile() {

        return this
            .apis
            .get(`/v1/users/profile`)
    }
}
