import {
    Component,
    OnInit,
    Input,
    Output,
    EventEmitter,
    SimpleChanges
} from '@angular/core';

import {FormGroup, FormControl, Validators, FormBuilder} from '@angular/forms';

@Component({selector: 'app-edit-user', templateUrl: './edit-user.component.html', styleUrls: ['./edit-user.component.scss']})
export class EditUserComponent implements OnInit {
    @Input()errorMessage : Error;
    @Input()user ;
    @Output()childSubmit : EventEmitter < any > = new EventEmitter < any > ();
    public templateForm : FormGroup;
    public dialog = false;
    @Input() groups:[];
    constructor(private formBuilder : FormBuilder,) {}

    ngOnInit() {

        this.createUserForm()
        this.dialog = true
        console.log(this.user)
    }

    private createUserForm() {

        console.log()

        this.templateForm = new FormGroup({
            name: this
                .formBuilder
                .group({
                    firstName: new FormControl(this.user.firstName),
                    lastName: new FormControl(this.user.lastName),
                    username: new FormControl(this.user.username),
                    gender: new FormControl(this.user.gender),
                    dateOfBirth: new FormControl(this.user.dateOfBirth),
                    initials: new FormControl(this.user.initials),
                    title: new FormControl(this.user.title),
                    id: new FormControl(this.user.id)
                }),

            contact: this
                .formBuilder
                .group({
                    email: new FormControl(this.user.email),
                    phoneNumber: new FormControl(this.user.phoneNumber)
                }),

            other: this
                .formBuilder
                .group({
                    driverLicenseNumber: new FormControl(this.user.driverLicenseNumber),
                    nationality: new FormControl(this.user.nationality),
                    nationalIdentificationNumber: new FormControl(this.user.nationalIdentificationNumber),
                    passportNumber: new FormControl(this.user.passportNumber),
                    groupId: new FormControl(this.user.group?.id)
                })

        });

    }

    onSubmit() {

        this
            .childSubmit
            .emit(this.templateForm.value)
    }

}
