import {
  Component,
  OnInit,
  Input,
  Output,
  EventEmitter,
  SimpleChanges,
} from '@angular/core';
import { FormGroup, FormControl, Validators ,FormBuilder} from '@angular/forms';

@Component({
  selector: 'app-group-authority-form',
  templateUrl: './group-authority-form.component.html',
  styleUrls: ['./group-authority-form.component.scss']
})
export class GroupAuthorityFormComponent implements OnInit {

  @Input()errorMessage : Error;
  @Input() groups:[];
  @Input() dialog :Boolean = false
  @Output() childSubmit : EventEmitter < any > = new EventEmitter < any > ();
  public templateForm : FormGroup;



  constructor(  private formBuilder : FormBuilder) {}
  ngOnInit() {

      this.createUserForm()

  }

  private createUserForm() {

      this.templateForm = new FormGroup({

                  name: new FormControl('', Validators.required),
                  description: new FormControl('', Validators.required)


      });

  }
  
  get f(){
    return this.templateForm.controls;
  }

  onSubmit() {

      this
          .childSubmit
          .emit(this.templateForm.value)
  }

}
