import {
  Component,
  OnInit,
  Input,
  Output,
  EventEmitter,
  SimpleChanges,
} from '@angular/core';
import { FormGroup, FormControl, Validators ,FormBuilder} from '@angular/forms';

@Component({
  selector: 'app-user-authority-form',
  templateUrl: './user-authority-form.component.html',
  styleUrls: ['./user-authority-form.component.scss']
})
export class UserAuthorityFormComponent implements OnInit {

  @Input()errorMessage : Error;
  @Input() auths:[];
  @Input() dialog :Boolean = false
   @Output()childSubmit : EventEmitter < any > = new EventEmitter < any > ();
  public templateForm : FormGroup;
  @Input() submitting:Boolean = false



  constructor(  private formBuilder : FormBuilder) {}
  ngOnInit() {

      this.createUserForm()

  }

  private createUserForm() {

      this.templateForm =  this.formBuilder.group({
        authorityIds: '',
        userId:''
       
      });

  }
 
  onSubmit() {

      this
          .childSubmit
          .emit(this.templateForm.value)
  }

}
