import {Injectable} from '@angular/core';
import {HttpParams} from '@angular/common/http';
import {ApisService} from './apis.service'
import {Answers, Page} from '../models'

@Injectable({
  providedIn: 'root'
})
export class AnswersService {

  constructor(private apis : ApisService) {}


  // tslint:disable-next-line: typedef
  getPaginatedAnswers( projId:number,filters?: any) {

      const httpParams = new HttpParams({fromObject: filters});

      return this.apis.get < Page < Answers >> (`/v1/surveys/all/by-project/${projId}`, httpParams)

  }



    OneAnswer(ansId){

      return this.apis.get(`/v1/surveys/${ansId}`)


   }

   getAllAnswers(projectId){



    return this.apis.get(`/v1/surveys/by-project/result-set/${projectId}`)


  }





}
