import {
  Component,
  OnInit,
  Input,
  Output,
  EventEmitter,
  SimpleChanges,
} from '@angular/core';
import { FormGroup, FormControl, Validators ,FormBuilder} from '@angular/forms';
import {DocumentsService,AlertService} from '../../../../services'
@Component({
  selector: 'app-document-form',
  templateUrl: './document-form.component.html',
  styleUrls: ['./document-form.component.css']
})
export class DocumentFormComponent implements OnInit {

  @Input()errorMessage : Error;
  @Input() groups:[];
  @Input() projects:[];
  @Input() dialog :Boolean = false
  @Input() submitting:Boolean = false
   @Output()childSubmit : EventEmitter < any > = new EventEmitter < any > ();
  file;
  public templateForm : FormGroup;



  constructor(  private documentService: DocumentsService, private alert: AlertService) {}
  ngOnInit() {

      this.createUserForm()
      this.documentService.getPaginatedDocuments({});

  }

  private createUserForm() {

      this.templateForm = new FormGroup({

                  file: new FormControl('', Validators.required),
                  clientId: new FormControl('',Validators.required)

              })



  }
  get f(){
    return this.templateForm.controls;
  }

  uploadFile(x){
    if(x.target.files.length>0){
      this.file = x.target.files[0]

    }
  }

  onSubmit() {
    let formData = new FormData()
    formData.append('file',this.file,this.file.name)
    console.log(this.file)
    this.documentService.uploadFile(formData).subscribe((res)=>{
      console.log(res)
      const newDoc:any={clientId:this.templateForm.value.clientId,fileName:res.fileName,name:this.file.name}

      this.documentService.newDocument(newDoc).subscribe((res)=>{
        this.alert.success('New Document uploaded');
        this.documentService.getPaginatedDocuments({});
      },(err)=>{
        this.alert.error(err.error.message);
      })
    },err=>{
      this.alert.error(err.error.message);
    })

      this
          .childSubmit
          .emit(this.templateForm.value)
  }

}

