import {
  Component,
  OnInit,
  Input,
  Output,
  EventEmitter,
  SimpleChanges,
} from '@angular/core';
import { ClrLoadingState } from '@clr/angular';
import { FormGroup, FormControl, Validators } from '@angular/forms';

@Component({
  selector: 'app-password-management-ui',
  templateUrl: './password-management-ui.component.html',
  styleUrls: ['./password-management-ui.component.scss']
})
export class PasswordManagementUiComponent implements OnInit {
  @Input() errorMessage: Error;
  @Input() btnState$: ClrLoadingState;
  @Input() loading :Boolean = false;
  @Output() childSubmit: EventEmitter<any> = new EventEmitter<any>();
  public managePasswordForm: FormGroup;

  constructor() {}

  ngOnInit() {
    this.createLoginForm();
    console.log(this.loading)
  }

  private createLoginForm() {
    this.managePasswordForm = new FormGroup({
      oldPassword: new FormControl('', Validators.required),
      password: new FormControl('', Validators.required),
      confirmPassword: new FormControl('', Validators.required)
    });
  }

  onSubmit() {
    this.childSubmit.emit(this.managePasswordForm.value);
  }
}
