import {Component, OnInit, Input, Output, EventEmitter} from '@angular/core';
import { ClrDatagridStateInterface } from '@clr/angular';
import {Projects} from '../../../../models'

@Component({
  selector: 'app-project-ui',
  templateUrl: './project-ui.component.html',
  styleUrls: ['./project-ui.component.scss']
})
export class ProjectUiComponent implements OnInit {

  @Input()errorMessage : Error;
  @Output()childSubmit : EventEmitter < any > = new EventEmitter < any > ();
  @Output() updatedState = new EventEmitter<ClrDatagridStateInterface>();
  @Output()edit = new EventEmitter < Projects[] > ()
  @Output()enable = new EventEmitter <Projects[]>()
  @Output() view = new EventEmitter <Projects[]>()
  @Output()authority = new EventEmitter <Projects[]>()
  @Input() total :number
  @Input() loading = false;
  dialog = false;
  @Input() projects : Projects;

  constructor() {}
  ngOnInit() {}



}
