import { Component, OnInit } from '@angular/core';
import { AgentsService } from '../../../../services';
import { ActivatedRoute, Router } from '@angular/router';
import { Agents,Users } from '../../../../models';

@Component({
  selector: 'app-view-agent',
  templateUrl: './view-agent.component.html',
  styleUrls: ['./view-agent.component.scss']
})
export class ViewAgentComponent implements OnInit {

  id: number;
  agent: Agents;
  user: Users;
  constructor(public agentService: AgentsService,

    private route: ActivatedRoute,
    private router: Router) { }

  ngOnInit() {
    this.id = this.route.snapshot.params.id;

    this.agentService.getOneAgent(`${this.id}`).subscribe((data: Agents) => {
      this.agent = data;
    
      console.log(data)
      
    });
  }

}
