import {Component, OnInit, Input, Output, EventEmitter} from '@angular/core';
import { ClrDatagridStateInterface } from '@clr/angular';
import {Agents ,Question} from '../../../../models'

@Component({
  selector: 'app-survey-list',
  templateUrl: './survey-list.component.html',
  styleUrls: ['./survey-list.component.scss']
})
export class SurveyListComponent implements OnInit {

  @Input()errorMessage : Error;

  @Output() updatedState = new EventEmitter<ClrDatagridStateInterface>();

  @Output() view = new EventEmitter <Question[]>()
  @Output() edit  = new EventEmitter <Question[]>()
  @Output() exportall = new EventEmitter <String>()
  @Output() assign = new EventEmitter < Agents[]>()
  @Output() unassign = new EventEmitter < Agents[]>()
  @Output() map = new EventEmitter<String>()
  @Output() del = new EventEmitter<String>()
  @Output() deactivate = new EventEmitter<String>()



  @Input() total :number
  @Input() loading = false;
  dialog = false;
  @Input() Surveys : Question;

  constructor() {}
  ngOnInit() {}



}
